<template>
	<view class="global-view">
		<view class="cover-box">
			<img src="/static/svg/default-cover.svg" alt="" srcset="">
		</view>
		<view class="container" id="the-article">
			<view class="title-author-and-time">
				<h1 class="article-details-title">{{article.title}}</h1>
				<view class="author-and-time">
					<span class="icon-with-text">
						<img src="/static/svg/author.svg" alt="" class="small-icon">
						{{article.author}}
					</span>
					<span class="icon-with-text">
						<img src="/static/svg/time.svg" alt="" class="small-icon">
						{{article.createTime}}
					</span>
				</view>
			</view>
			<hr class="article-horizonal-line">
			<view class="views-likes-favorites">
				<span id="views" class="icon-with-text">
					<img src="/static/svg/views.svg" alt="" class="small-icon">
					{{article.views}}
				</span>
				<span id="likes" class="icon-with-text">
					<img src="/static/svg/like.svg" alt="" class="small-icon">
					{{article.likes}}
				</span>
				<span id="favorites" class="icon-with-text">
					<img src="/static/svg/favorites.svg" alt="" class="small-icon">
					{{article.favorites}}
				</span>
			</view>
			<hr class="article-horizonal-line">
			<view class="article-content">
				<zero-markdown-view :themeColor="themeColor" :markdown="article.markdownContent">
					
				</zero-markdown-view>
			</view>
		</view>
		<view class="container" id="tags">
			<span>Tag</span>
			<span>Tag</span>
			<span>Tag</span>
			<span>Tag</span>
			<span>Tag</span>
			<span>Tag</span>
		</view>
		<view class="container" id="button-likes-and-favorties">
			<button class="likes-button">点赞</button>
			<button class="favorites-button">收藏</button>
		</view>
		<view class="container" id="comments" v-for="item of comments">
			<span>
				<p>评论({{article.comments}})</p>
				<button class="new-comment-button">发表评论</button>
			</span>
			<hr class="article-horizonal-line">
			<view class="comment-box" v-for="item of comments" :key="-item.cid">
				<view class="comment-user-avatar-and-nickname">
					<img src="/static/svg/default-avatar.svg" alt="">
					<view class="user-and-date">
						<span>{{item.nickname}}</span>
						<span>{{item.ctime}}</span>
					</view>
				</view>
				<view class="comment-content">
					这是一条评论
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		marked
	} from 'marked';

	export default {
		data() {
			return {
				themeColor: "#000000",
				article: {
					aid: 0,
					title: "样例文本",
					author: "Sample",
					category: "示例分类",
					createTime: "2023-08-20",
					tabloid: "横批无丁点，白云深处有人家。此地一为别，孤蓬万里征。绿海无波平，红尘里作琴瑟声...",
					views: 1145141919810,
					likes: 114514,
					favorites: 11451,
					comments: 1145,
					cover: "/static/svg/default-cover.svg",
					markdownContent: ""
				},
				comments: [{
					cid: 0,
					ctext: "这是一条评论",
					uid: 0,
					ctime: "2023-08-20",
					nickname: "评论用户"
				}]
			}
		},
		computed: {
			htmlContent() {
				return marked(this.article.markdownContent);
			}
		},
		onLoad(para) {
			this.loadFile();
		},
		methods: {
			async loadFile() {
				try {
					const response = await fetch("/static/README.md");
					if (!response.ok) {
						throw new Error("Network response was not ok");
					}
					this.article.markdownContent = await response.text();
					console.log(this.article.markdownContent);
				} catch (error) {
					console.error("There was a problem with the fetch operation:", error.message);
				}
			}
		}
	}
</script>

<style>
	@import url("../../static/css/global-ui.css");
	@import url("/components/gaoyia-parse/parse.css");
</style>